//@use 'variables';
/* Fade-in animation for dialog */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Fade-in animation for backdrop */
@keyframes backdropFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.custom-container {
  background: rgb(248, 248, 248);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 560px;
  width: 100%;
  animation: fadeIn 0.3s ease;
  margin: auto;

  @media(max-width: 600px){
    width: 95%;
  }

  h2{
    opacity: .8;
    font-size: 1rem; 
    font-weight: 700; 
  }


  label {
    display: block;
    font-weight: 600;
    font-size: 14px;
    margin: 1.5rem 0 10px 5px;
    opacity: .75;
  }

  .coll_name {
    width: 100%;
    padding: 8px;
    margin-top: 1.5rem;
    border: 1px solid #ebebeb;
    border-radius: 4px;
  }

  .field_sect{
    margin-top: 1.75rem;
    padding: .65rem .45rem;
    border: 1.6px solid #ccc;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h3{
      font-size: 1rem;
      opacity: .75;
      font-weight: 600;
      text-align: left;
      width: 100%;
    }

    #fields_explained {
      display: none; /* Hidden by default */
      text-align: center;
      color: #666;
      font-size: 14px;
      padding: 10px;
      border: 1px dashed #b9b8b8;
      background-color: #f9f9f9;
      border-radius: 6px;
      width: 100%;
      margin-top: 1rem;
    }
    
    #fields_explained p {
      margin: 0;
    }
    
    #fields_explained ul {
      margin: 10px 0 0 0;
      padding: 0;
      list-style: none;
    }
    
    #fields_explained li {
      margin: 5px 0;
    }

    .field-group {
      display: grid;
      grid-template-columns: 1fr 1fr 20px;
      gap: 8px;
      width: 100%;
      margin-top: .7rem;
      height: 36px;
      padding: 0 .25rem;
  
      input{
        padding: 5px 8px;
        border: 1px solid #ebebeb;
        border-radius: 4px;
        height: 100%;
      }
  
      select {
        height: 100%;
        padding: 5px 8px;
        margin-bottom: 10px;
        border: 1px solid #ddd;
        border-radius: 4px;
      }
  
      .remove-btn {
        background-color: #f72b4d;
        color: white;
        border: none;
        cursor: pointer;
        font-size: 16px;
        font-weight: 700;
        border-radius: 4px;
        width: 26px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .25s ease;
      }
    
      .remove-btn:hover {
        background-color: rgb(219, 14, 48);
      }  
    }
  
    #addFieldBtn{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      width: fit-content;
      padding: .5rem .65rem;
      margin-top: 2rem;
      margin-bottom: -2rem;
      border: none;
      border-radius: 4px;
      background-color: #ebf2f7;
      border: 2px solid gray;
      cursor: pointer;
      transition: .25s ease;

      img{
        width: 24px;
        opacity: .75;
        transition: .25s ease;

        @media(max-width: 600px){
          width: 22px;
        }
      }
      span{
        color: rgb(31, 30, 30);
        transition: .25s ease;
        font-size: 16px;
        font-weight: 600;
        margin-left: .8rem;
        margin-bottom: -2px;

        @media(max-width: 600px){
          font-size: 14px;
        }
        @media(max-width: 330px){
          font-size: 12px;
        }
      }

      &:hover{
        background-color: #cbd3da;
        border: 2px solid #190c3b;

        img{
          opacity: 1;
        }
        span{ 
          color: black;
        }
      }
    }
  }

  #createCollectionBtn{
    display: block;
    width: 100%;
    padding: 10px;
    margin-top: 3rem;
    border: none;
    border-radius: 4px;
    background-color: #12c47a;
    color: white;
    font-size: 16px;
    font-weight: 600;
    transition: .25s ease;
    cursor: pointer;

    &:hover {
      background-color: #0dca8e;
      color: rgb(29, 28, 28);
    }
  }
  
  #responseMessage {
    margin-top: 15px;
    text-align: center;
    font-weight: bold;
  }

  &::backdrop {
    background: rgba(0, 0, 0, 0.5); 
    backdrop-filter: blur(2px);
    animation: backdropFade 0.3s ease;
  }
}
